import React, {Component} from 'react';
import Header from './Header';
import Footer from './Footer';
import '../Styles/Contacts.css';
import team_bg from '../media/team_bg.svg';
import {connect} from 'react-redux';
import eng from '../localization/eng/crew';
import ru from '../localization/ru/crew';
import { seoActions } from '../actions/seoActions';
import MetaTags from "react-meta-tags";

const getSeoByUrl = seoActions.getSeoByUrl;

class Contacts extends Component {

    constructor(props){
        super(props);
    }

    componentDidMount(){
        this.props.getSeoByUrl('contacts')
            .then(data => console.log(data))
    }


    render(){
        let metaTags = this.props.getMetaResult && this.props.getMetaResult.result;
        return(
            <div className='contacts_page'>
             <MetaTags>
            <title>{metaTags && metaTags.title}</title>
            <meta name="description" content={metaTags && metaTags.description} />
            <meta name="keywords" content={metaTags && metaTags.keywords} />
            </MetaTags>
                <Header page='contacts'/>
                <section className='contacts_section'>
                   <div className='container'>
                       <h2>#CONTACTS</h2>
                       <ul className='contacts_main_info_box'>
                           <li>email: <span>example@jemmi.com</span></li>
                           <li>tell. <span>+380 66 7680876</span></li>
                           <li>head office: <br/>Production service Ukrain <span><br/>Test Drive 13 St.</span> , -1 floor <br/>0678 Kiev</li>
                       </ul>
                       <div className='open_map_button' style={{background: '#000'}}>
                            <span style={{color: '#fff'}}>OPEN MAP</span>
                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 21.767 31.777">
                                <path fill='#000' d="M116.4,20V34.406a3.829,3.829,0,0,0,3.558,3.947h0a3.692,3.692,0,0,0,3.558-3.947h0a1.35,1.35,0,0,1,1.256-1.382h2.512a1.35,1.35,0,0,1,1.256,1.382V46.248a5.04,5.04,0,0,0,4.814,5.329h0a5.04,5.04,0,0,0,4.814-5.329L137.958,19.8Z" transform="translate(-116.4 -19.8)"/>
                            </svg>
                        </div>
                   </div>
                </section>
                <section className='team_section'>
                    {
                        this.props.lang === 'ru' ? ru.team(team_bg) : eng.team(team_bg)
                    }
                    {/* <div className='container'>
                        <h2>TEAM</h2>
                        <div className='team_info_box'>
                            <div className='team_text_box'>
                                <p>Ut wisi enim ad minim veniam, quis nostrud exerci tation ullamcorper suscipit lobortis nisl ut aliquip ex ea commodo consequat. Duis autem vel eum iriure dolor in hendrerit in vulputate velit esse molestie consequat, vel illum dolore eu feugiat nulla facilisis at vero eros et accumsan et iusto odio dignissim qui blandit praesent luptatum zzril delenit augue duis dolore te feugait nulla facilisi</p>
                                <p>Dignissim qui blandit praesent luptatum zzril delenit augue duis dolore te feugait nulla facilisi</p>
                            </div>
                            <img src={team_bg} alt='' />
                        </div>
                    </div> */}
                </section>
                <Footer/>
            </div>
        )
    }

}

const mapStateToProps = (state) => {
    return {
        lang: state.lang.currentLang,
        getMetaResult: state.seo.getSeoByUrlResult
    }
};

export default connect(mapStateToProps, { getSeoByUrl })(Contacts);