import React from 'react'
import { convert } from '../utils/dateConvertor';
import { connect } from 'react-redux';
import { readMore } from '../utils/readMore';
const noImage = 'https://upload.wikimedia.org/wikipedia/commons/thumb/6/6c/No_image_3x4.svg/1280px-No_image_3x4.svg.png';

function Post(props) {
  return (
      <div className='blog_post_box'>
        <div className='blog_media_box'>
            {
              props.post.publishedDate && 
              <div className='blog_date_box date_post_blog'>
                <span className='blog_post_day'>{convert(props.post.publishedDate).y}</span>
                <span className='blog_post_other'>{convert(props.post.publishedDate).dm}</span>
              </div>
            }
            <div className='blog_picture_section'>
                <div className='blog_picture_box'>
                {
                    props.post.image && <img src={props.post.image.secure_url || noImage} alt='' />
                }
                </div>
            </div>
        </div>
        <div className='blog_info_box'>
            <h2>{props.lang === 'ru' ? props.post.titleRu : props.post.title}</h2>
            <p dangerouslySetInnerHTML={{__html: props.lang === 'ru' ?  props.post.contentRu.extended : props.post.content.extended}}>
            </p>
        </div>
        {readMore('/post/'+props.post.url)}
    </div>
  )
}

const mapStateToProps = (state) => {
    return {
        lang: state.lang.currentLang
    }
};

export default connect(mapStateToProps)(Post);
