import React, {Component} from 'react';
import { Link } from 'react-router-dom';
import Header from './Header';
import Footer from './Footer';
import * as d3 from 'd3';
import logo from '../media/gallery_logo.svg';
import icon from '../media/gallery_icon.svg';
import test_image from '../media/locations_bg.jpg';
import '../Styles/Locations.css';
import { apiUrl } from '../config';
import ru from '../localization/ru/locations'
import eng from '../localization/eng/locations'
import { connect } from 'react-redux';
import { seoActions } from '../actions/seoActions';
import MetaTags from "react-meta-tags";

const getSeoByUrl = seoActions.getSeoByUrl;
const noImage = 'https://upload.wikimedia.org/wikipedia/commons/thumb/6/6c/No_image_3x4.svg/1280px-No_image_3x4.svg.png';

class Locations extends Component {

    constructor(props){
        super(props);
        this.state = {
            locations: {}
        }
    }

    componentDidMount(){
        this.props.getSeoByUrl('locations')
        fetch(`${apiUrl}/locations`)
            .then(res => res.json())
            .then(locations => this.setState({locations}))
            .then(() => this.preSets())

    }

    preSets(){
        let that = this;
        d3.selectAll('.gallery_box_description')
            .style('opacity', '0');
        d3.selectAll('.location_box')
            .on('mouseover', function(){
                d3.select(this).call(that.descriptionAnimation, false);
            })
            .on('mouseleave', function(){
                d3.select(this).call(that.descriptionAnimation, true);
            })
    }

    descriptionAnimation(e, isOpen){
        e.select('.gallery_box_description')
            .transition().duration(300).ease(d3.easeCubic)
            .style('opacity', `${isOpen ? '0' : '1'}`);
    }


    readMore(url){
        return (
            <Link to={`location/${url}`}>
                <div className='gallery_read_more' style={{background: '#000'}}>
                    <span style={{color: '#fff'}}>Read more</span>
                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 21.767 31.777">
                        <path fill='#000' d="M116.4,20V34.406a3.829,3.829,0,0,0,3.558,3.947h0a3.692,3.692,0,0,0,3.558-3.947h0a1.35,1.35,0,0,1,1.256-1.382h2.512a1.35,1.35,0,0,1,1.256,1.382V46.248a5.04,5.04,0,0,0,4.814,5.329h0a5.04,5.04,0,0,0,4.814-5.329L137.958,19.8Z" transform="translate(-116.4 -19.8)"/>
                    </svg>
                </div>
            </Link>
        )
    }

    render(){
        let metaTags = this.props.getMetaResult && this.props.getMetaResult.result;
        return(
            <div className='locations_page'>
 <MetaTags>
            <title>{metaTags && metaTags.title}</title>
            <meta name="description" content={metaTags && metaTags.description} />
            <meta name="keywords" content={metaTags && metaTags.keywords} />
            </MetaTags>
                <Header page='locations'/>
                {
                    this.props.lang === 'ru' ? ru.marvelous() : eng.marvelous()
                }
                {/* <section className='locations_preview'>
                    <div className='container'>
                        <h2>VIEW OF UKRAINE</h2>
                        <p>Country where the present and the past live side by side will open you the new horizons of imagination.  Every scene is completely special. Romantic old classical architecture,  the impressive modern arts,  untouched wild nature,  simplicity of busy streets.  Areas full of beauty, dreams, emotions, history and mystery…. And every artist will find out the unique way of expressing the world, this one of the best sources of enjoyment.</p>
                    </div>
                </section> */}
                {
                    this.props.lang === 'ru' ? ru.provide() : eng.provide()
                }
                {/* <section className='locations_info'>
                    <div className='container'>
                        <h3>Our company provide you with all necessities on a set:</h3>
                        <div className='locations_line_box'>
                            <ul>
                                <li>Permits</li>
                                <li>Police and Traffic control</li>
                                <li>Medic personnel</li>
                                <li>Safety personnel</li>
                                <li>Street closures and running blocks</li>
                            </ul>
                            <ul>
                                <li>Crowd control</li>
                                <li>Map creation</li>
                                <li>Location Vehicles and Craft Services</li>
                            </ul>
                        </div>
                        <div className='locations_contact_box'>
                            <p>Please 
                                <span className='locations_contact_us' style={{background: '#000', color:'#fff'}}>
                                    contact us 
                                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 21.767 31.777">
                                        <path fill='#000' d="M116.4,20V34.406a3.829,3.829,0,0,0,3.558,3.947h0a3.692,3.692,0,0,0,3.558-3.947h0a1.35,1.35,0,0,1,1.256-1.382h2.512a1.35,1.35,0,0,1,1.256,1.382V46.248a5.04,5.04,0,0,0,4.814,5.329h0a5.04,5.04,0,0,0,4.814-5.329L137.958,19.8Z" transform="translate(-116.4 -19.8)"/>
                                    </svg>
                                </span> 
                                for more details...
                            </p>
                        </div>
                    </div>
                </section> */}




                <section className='locations_gallery'>
                    <div className='container'>
                        <div className='gallery_logo_box'>
                            <img src={logo} alt='' />
                            <span>@jem_production</span>
                        </div>
                        <div className='locations_gallery_box'>
                        {
                          this.state.locations.length > 0 && this.state.locations.map(location => (
                            <div className='location_box'>
                                <img src={location.heroImage.secure_url || noImage} alt='' />
                                <div className='gallery_box_description'>
                                    <img src={icon} alt='' />
                                    <p dangerouslySetInnerHTML={{__html:location.content.brief}}/>
                                    {this.readMore(location.url)}
                                </div>
                            </div>
                          ))
                        }
                        </div>
                        <div className='location_gallery_line'>
                            <div className='location_action_button'>
                                <span>{this.props.lang === 'ru' ? 'Подписаться в Instagram' : 'Follow on instagram'}</span>
                                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 21.767 31.777">
                                    <path fill='#000' d="M116.4,20V34.406a3.829,3.829,0,0,0,3.558,3.947h0a3.692,3.692,0,0,0,3.558-3.947h0a1.35,1.35,0,0,1,1.256-1.382h2.512a1.35,1.35,0,0,1,1.256,1.382V46.248a5.04,5.04,0,0,0,4.814,5.329h0a5.04,5.04,0,0,0,4.814-5.329L137.958,19.8Z" transform="translate(-116.4 -19.8)"/>
                                </svg>
                            </div>
                            <div className='location_action_button'>
                                <span>{this.props.lang === 'ru' ? 'Загрузить больше' : 'Load more'}</span>
                                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 21.767 31.777">
                                    <path fill='#000' d="M116.4,20V34.406a3.829,3.829,0,0,0,3.558,3.947h0a3.692,3.692,0,0,0,3.558-3.947h0a1.35,1.35,0,0,1,1.256-1.382h2.512a1.35,1.35,0,0,1,1.256,1.382V46.248a5.04,5.04,0,0,0,4.814,5.329h0a5.04,5.04,0,0,0,4.814-5.329L137.958,19.8Z" transform="translate(-116.4 -19.8)"/>
                                </svg>
                            </div>
                        </div>
                    </div>
                </section>
                <Footer/>
            </div>
        )
    }

}

const mapStateToProps = (state) => {
    return {
        lang: state.lang.currentLang,
        getMetaResult: state.seo.getSeoByUrlResult
    }
};

export default connect(mapStateToProps, { getSeoByUrl })(Locations);