import React, {Component} from 'react';
import Header from './Header';
import Slider from './Slider';
import ServiceBar from './ServiceBar';
import Footer from './Footer';
import '../Styles/Main.css';
import { connect } from 'react-redux';
import ru from '../localization/ru/main';
import eng from '../localization/eng/main';
import { seoActions } from '../actions/seoActions';
import MetaTags from "react-meta-tags";

const getSeoByUrl = seoActions.getSeoByUrl;

class Main extends Component {

    constructor(props){
        super(props);
     
    }

    componentDidMount(){
        this.props.getSeoByUrl('main')
    }

    readMore(url, color){
        return (
            <a to={url}>
                <div className='read_more' style={{background: color}}>
                    <span style={{color: `${color === '#fff' ? '#000' : '#fff'}`}}>Read more</span>
                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 21.767 31.777">
                        <path fill={color} d="M116.4,20V34.406a3.829,3.829,0,0,0,3.558,3.947h0a3.692,3.692,0,0,0,3.558-3.947h0a1.35,1.35,0,0,1,1.256-1.382h2.512a1.35,1.35,0,0,1,1.256,1.382V46.248a5.04,5.04,0,0,0,4.814,5.329h0a5.04,5.04,0,0,0,4.814-5.329L137.958,19.8Z" transform="translate(-116.4 -19.8)"/>
                    </svg>
                </div>
            </a>
        )
    }

    render(){
        let metaTags = this.props.getMetaResult && this.props.getMetaResult.result;
        return(
            <div className='main_page'>
 <MetaTags>
            <title>{metaTags && metaTags.title}</title>
            <meta name="description" content={metaTags && metaTags.description} />
            <meta name="keywords" content={metaTags && metaTags.keywords} />
            </MetaTags>
                <Header page='main'/>
                <Slider/>
                {
                    this.props.lang === 'ru' ? ru.main_services(this.readMore) : eng.main_services(this.readMore)
                }
                {/* <section className='main_services'>
                    <div className='container'>
                        <h3>OFFERING LOCAL PRODUCTION</h3>
                        <p>Our company offers a full range of services on organising every need during production period.</p>
                        <ServiceBar/>
                        {this.readMore('#', '#000')}
                    </div>
                </section> */}
                {
                    this.props.lang === 'ru' ? ru.marvelous(this.readMore) : eng.marvelous(this.readMore)
                }
                {/* <section className='marvelous'>
                    <div className='container'>
                        <h3>MARVELOUS SCENERY OF UKRAINE</h3>
                        <p>Country where the present and the past live side by side will open you the new horizons of imagination.  Every scene is completely special. Romantic old classical architecture,  the impressive modern arts,  untouched wild nature,  simplicity of busy streets.  Areas full of beauty, dreams, emotions, history and mystery…. And every artist will find out the unique way of expressing the world, this one of the best sources of enjoyment.</p>
                        {this.readMore('#', '#fff')}
                    </div>
                </section> */}
                {
                    this.props.lang === 'ru' ? ru.pricing(this.readMore) : eng.pricing(this.readMore)
                }
                {/* <section className='price'>
                    <div className='container'>
                        <h3>PRICE OF OUR SERVICES</h3>
                        <p>Jem helps to create all types of media projects – commercial shoots, photo shoots, feature films, television, corporate video content and music videos.</p>
                        {this.readMore('#', '#000')}
                    </div>
                </section> */}
                <Footer/>
            </div>
        )
    }

}


const mapStateToProps = (state) => {
    return {
        lang: state.lang.currentLang,
        getMetaResult: state.seo.getSeoByUrlResult
    }
};

export default connect(mapStateToProps, { getSeoByUrl })(Main);