import React, {Component} from 'react';
import Logo from '../media/JemLogo.svg';
import '../Styles/Header.css';
import * as d3 from 'd3';
import ls from 'local-storage';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';


class Header extends Component {

    constructor(props){
        super(props);
        this.state = {
            menuVisibility: false
        }
    }

    componentDidMount(){
        if(this.props.page){
            this.setPage();
        } 
        this.setLang(this.props.lang);
        this.setMenu();
        
        let that = this,
            timeout = false, 
            delay = 0;

        window.addEventListener('resize', function() {
            clearTimeout(timeout);
            timeout = setTimeout(that.getDimensions(), delay);
        });

        that.getDimensions();
    }

    changeLang = lang => {
        if(lang === 'ru'){
            this.props.changeToRu()
        } else {
            this.props.changeToEn()
        }
    }

    getDimensions(){
        if(window.innerWidth > 960){
            d3.select('.header_nav_box')
                .style('display', 'flex');
            d3.select('.header_container')
                .style('height', `70px` )
                .style('padding', '');
        }
        else{
            this.setState({menuVisibility: true}, this.setMenuStyle(true));
        }
    }

    setPage(){
        let that = this;
        let menu_buttons = d3.selectAll('.header_nav_box li').nodes();
        let isPage = false;
        for(let i = 0; i < menu_buttons.length; i++){
            let pageName = menu_buttons[i].innerHTML.toLowerCase();
            if(pageName === that.props.page){
                this.setButtonStyle(menu_buttons[i], true);
                isPage = true;
            }else{
                this.setButtonStyle(menu_buttons[i], false);
            }
        }
    }

    setButtonStyle(button, isActive){
        d3.select(button)
            .style('bottom', `${isActive ? 5 : 0}px`)
            .classed('inactive', !isActive)
            .classed('active', isActive);
    }

    setLang(lang){
        this.changeLang(lang)
        d3.selectAll('.header_lang_box span')
            .each(function(){
                let e = d3.select(this);
                if(e.node().innerHTML.toLowerCase() === lang) {
                    e.style('color', '#FFB100')
                     .style('text-decoration', 'underline');
                } else {
                    e.style('color', '#5C5C5C')
                     .style('text-decoration', 'none');
                }
            })
    }

    setMenu(){
      let that = this;
      d3.select('.header_burger').on('click', () => { that.setMenuStyle() });
    }

  setMenuStyle(isResize = false){
    let that = this;
      if(!isResize){
        that.setState({menuVisibility: !that.state.menuVisibility}, function(){
          d3.select('.header_nav_box')
            .style('display', `${!that.state.menuVisibility ? 'flex' : 'none'}`);
          d3.select('.header_container')
            .style('height', `${!that.state.menuVisibility ? '80vh' : '60px'}` )
            .style('padding', `${!that.state.menuVisibility ? '10vh 0vh 10vh 0vh' : '0'}`);
        });
      }else{
        that.setState({menuVisibility: !that.state.menuVisibility}, function(){
          d3.select('.header_nav_box')
            .style('display', 'none');
          d3.select('.header_container')
            .style('height', '60px' )
            .style('padding', '0');
          });
      }
  }

    showMenu(){
        d3.select('.header_nav_box')
            .style('display', 'flex');
        d3.select('.header_container')
            .style('height', '80vh')
            .style('padding', '10vh 0vh 10vh 0vh');
    }

    hideMenu(){
        d3.select('.header_nav_box')
            .style('display', 'none');
        d3.select('.header_container')
            .style('height', '60px')
            .style('padding', '0');
    }

    render(){
        return(
            <section className='header'>
                
               {
                   this.props.page &&
                    <div className='header_container'>
                        <div className='header_logo_box'>
                            <a href='/'><img alt='' src={Logo} /></a>
                        </div>
                        <ul className='header_nav_box'>
                            <a href='/service'><li>{ this.props.lang === 'ru' ? 'СЕРВИС' : 'SERVICE'}</li></a>
                            <a href='/locations'><li>{ this.props.lang === 'ru' ? 'ЛОКАЦИИ' : 'LOCATIONS'}</li></a>
                            <a href='/budget'><li>{ this.props.lang === 'ru' ? 'БЮДЖЕТ' : 'BUDGET'}</li></a>   
                            <a href='/support'><li>{ this.props.lang === 'ru' ? 'ПОМОЩЬ' : 'SUPPORT'}</li></a>
                            <a href='/work'><li>{ this.props.lang === 'ru' ? 'РАБОТА' : 'WORK'}</li></a>
                            <a href='/contacts'><li>{ this.props.lang === 'ru' ? 'КОНТАКТЫ' : 'CONTACTS'}</li></a>
                            <a href='/blog'><li>{ this.props.lang === 'ru' ? 'БЛОГ' : 'BLOG'}</li></a>
                        </ul>
                        <div className='header_lang_box'>
                            <span onClick={()=>{this.setLang('en')}}>EN</span>
                            <span onClick={()=>{this.setLang('ru')}}>RU</span>
                        </div>
                        <div className='header_burger'>
                            <span/>
                        </div>
                    </div>
                }
            </section>
        )
    }

}

const mapStateToProps = (state) => {
    console.log(state.lang)
    return {
      lang: state.lang.currentLang
    }
  };
  
  
  const mapDispatchToProps = dispatch => (
    bindActionCreators({
      changeToRu: () => dispatch({ type: 'CHANGE_TO_RU' }),
      changeToEn: () => dispatch({ type: 'CHANGE_TO_EN' })
    }, dispatch)
  );
  
  
  export default connect(mapStateToProps, mapDispatchToProps)(Header);
  