const initialState = {};

export const seoReducer = (state = initialState, action) => {
  switch (action.type) {
    case 'GET_SEO_PAGES':
      return {
        ...state,
        getSeoResult: action.payload
      };
    case 'EDIT_SEO_PAGE':
      return {
        ...state,
        editResult: action.payload
      };
    case 'GET_SEO_BY_URL':
      return {
        ...state,
        getSeoByUrlResult: action.payload
      };  
    case 'GET_ARTICLE_SEO_BY_URL':
      return {
        ...state,
        getArticleSeoByUrlResult: action.payload
      }; 
    default:
      return state;
  }
}