import React, {Component} from 'react';
import '../Styles/Footer.css';
import instagram_logo from '../media/logo_instagram.svg';
import facebook_logo from '../media/logo_facebook.svg';
import logo from '../media/jem_logo_white.svg';
import rights from '../media/equinox_rights.svg';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';


class Footer extends Component {


    constructor(props){
        super(props);
        this.state = {
        }
    }


    // localStorageUpdated(){
    //     console.log(localStorage.getItem('lang'))
    //     this.setState({lang: localStorage.getItem('lang')})
    // }



 

    render(){
        return(
            <section className='footer'>
                <div className='container'>
                    <div className='footer_box_line'>
                        <div className='footer_box'>
                            <div className='footer_box_line'>
                                <div className='footer_box footer_contacts'>
                                    { this.props.lang === 'en' ? <h4>GET IN TOUCH WITH US</h4> : <h4>Свяжитесь с нами</h4>}
                                    <p>Ukraine +380 658976189</p>
                                    <p>example@jemmi.com</p>
                                </div>
                                <div className='footer_box'>
                                    <h4>{this.props.lang === 'en' ? 'FOLLOW US:' : 'ПОДПИШИТЕСЬ НА НАС'}</h4>
                                    <div className='footer_box_line footer_follows'>
                                        <img src={instagram_logo} alt='' />
                                        <img src={facebook_logo} alt='' />
                                    </div>
                                </div>
                            </div>
                            <p className='footer_info'>{this.props.lang === 'en' ? 'Professional team, marvelous  locations, technical possibilities,  affordable prices will make your experience unforgettable!' : 'Профессиональная команда, удивительные локации, технические возможности и приемлемая цена при создании незабываемых эмоций!'}</p>
                            <div className='footer_box_line footer_lang'>
                                <span onClick={() => this.props.changeToEn()}>English</span>
                                <span onClick={() => this.props.changeToRu()}>Русский</span>
                            </div>
                        </div>
                        <div className='footer_box'>
                            <img src={logo} alt='' />
                            <img src={rights} alt='' />
                        </div>
                    </div>
                    <p>© JEM Production™, 2019 - Privacy Policy </p>
                </div>
            </section>
        )
    }

}

const mapStateToProps = (state) => {
    return {
        lang: state.lang.currentLang
    }
};

const mapDispatchToProps = dispatch => (
    bindActionCreators({
      changeToRu: () => dispatch({ type: 'CHANGE_TO_RU' }),
      changeToEn: () => dispatch({ type: 'CHANGE_TO_EN' })
    }, dispatch)
  );

export default connect(mapStateToProps, mapDispatchToProps)(Footer);