import React, {Component} from 'react';
import AliceCarousel from 'react-alice-carousel';
import * as d3 from 'd3';
import "react-alice-carousel/lib/alice-carousel.css";
import '../Styles/Slider.css';
import picture_1 from '../media/main_slider_1.jpg';
import picture_2 from '../media/main_slider_1.jpg';



export default class extends Component {

    constructor(props){
        super(props);
        this.state = {
            currentIndex: 0,
            responsive: { 1024: { items: 1 } },
            galleryItems: this.galleryItems(),
        } 
    }

    items = [
        {
            image: picture_1,
            text: 'UKRAINIAN PRODUCTION SERVICE'
        },
        {
            image: picture_2,
            text: 'UKRAINIAN PRODUCTION SERVICE'
        },
        {
            image: picture_2,
            text: 'UKRAINIAN PRODUCTION SERVICE'
        },
        {
            image: picture_2,
            text: 'UKRAINIAN PRODUCTION SERVICE'
        },
        {
            image: picture_2,
            text: 'UKRAINIAN PRODUCTION SERVICE'
        }
    ];

    componentDidMount(){
        this.checkControlIndex(0);
    }

   
    slideTo = (i) => {
        this.setState({ currentIndex: i });
        this.checkControlIndex(i);
    }

    checkControlIndex = (i) => {
        d3.selectAll('.slider_navigation span').nodes().forEach(function(value, k){ 
            d3.select(value).style('background', `${k !== i ? 'none' : 'black'}`);
        });
    }
   
    onSlideChanged = (e) => this.setState({ currentIndex: e.item })
   
    slideNext = () => this.setState({ currentIndex: this.state.currentIndex + 1 })
   
    slidePrev = () => this.setState({ currentIndex: this.state.currentIndex - 1 })
   
    thumbItem = (item, i) => <span key={i} onClick={() => this.slideTo(i)}></span>
   
    galleryItems() {
      return this.items.map((i) => 
            <div key={i} className='slider_image_box'>
                <div className='slider_text_box'>
                    <h2>{i.text}</h2>
                </div>
                <img alt='' src={i.image}/> 
            </div>
        )
    }

    render(){
        const { responsive, currentIndex, galleryItems } = this.state;
        return(
            <div>    
                <div className='slider_navigation'>
                    <ul>{this.items.map(this.thumbItem)}</ul>   
                </div>    
                <AliceCarousel
                    dotsDisabled={true}
                    buttonsDisabled={true}
                    items={galleryItems}
                    responsive={responsive}
                    slideToIndex={currentIndex}
                    onSlideChanged={this.onSlideChanged}
                    />
            </div>
        )
    }
}