import React, {Component} from 'react';
import Header from './Header';
import Footer from './Footer';
import * as d3 from 'd3';
import '../Styles/Post.css';
import { apiUrl } from '../config';
import { Redirect, Link } from 'react-router-dom';
import { convert } from '../utils/dateConvertor';
import { connect } from 'react-redux';
import test_img from '../media/project_test_image.jpg';
const noImage = 'https://upload.wikimedia.org/wikipedia/commons/thumb/6/6c/No_image_3x4.svg/1280px-No_image_3x4.svg.png';

class BlogPost extends Component {

    constructor(props){
        super(props);
        this.state = {
            post: {},
            error: false
        }
    }

    componentDidMount(){
        const articleUrl = this.props.match.url.split('/')[2];
        fetch(`${apiUrl}/article/${articleUrl}`)
            .then(res => {
                if(res.status === 404) {
                    this.setState({ error: true })
                }
                else return res.json();
            })
            .then(post => this.setState({post}))
    }
    

    


    render(){
        if(!this.state.error){
        return(
            <div className='blog_post_view'>
                <Header page='blog'/>
                <section className='blog_post_section'>
                {
                    this.state.post.publishedDate &&
                    <div className='blog_date_box  date_post_view'>
                        <span className='blog_post_day'>{convert(this.state.post.publishedDate).y}</span>
                        <span className='blog_post_other'>{convert(this.state.post.publishedDate).dm}</span>
                    </div>
                }
                   <div className='post_view_container' id='test'>
                        <Link to="/blog" className='post_button blog_post_exit_button'>
                            <span style={{color: '#000'}}>Go back</span>
                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 21.767 31.777">
                                <path fill='#fff' d="M116.4,20V34.406a3.829,3.829,0,0,0,3.558,3.947h0a3.692,3.692,0,0,0,3.558-3.947h0a1.35,1.35,0,0,1,1.256-1.382h2.512a1.35,1.35,0,0,1,1.256,1.382V46.248a5.04,5.04,0,0,0,4.814,5.329h0a5.04,5.04,0,0,0,4.814-5.329L137.958,19.8Z" transform="translate(-116.4 -19.8)"/>
                            </svg>
                        </Link>
                        <div className='blog_post_view_info_box'>
                            <h1> { this.props.lang === 'ru' ? this.state.post.titleRu : this.state.post.title } </h1>
                            {
                                this.state.post.content && <p>{ this.props.lang === 'ru' ? this.state.post.contentRu.brief : this.state.post.content.brief }</p>
                            }
                             {
                             this.state.post.image && <img src={ this.state.post.image.secure_url || noImage } alt=''/>
                             }
                            {
                                this.state.post.content && <p>{ this.props.lang === 'ru' ? this.state.post.contentRu.extended : this.state.post.content.extended }</p>
                            }
                        </div>
                   </div>
                </section>
                <Footer/>
            </div>
        )} else {
            return(<Redirect to="/blog"/>)
        }
    }

}

const mapStateToProps = (state) => {
    return {
        lang: state.lang.currentLang
    }
};

export default connect(mapStateToProps)(BlogPost);