import React, {Component} from 'react';
import Header from './Header';
import Footer from './Footer';
import * as d3 from 'd3';
import '../Styles/Support.css';
import { connect } from 'react-redux';
import ru from '../localization/ru/support';
import eng from '../localization/eng/support';
import { seoActions } from '../actions/seoActions';
import MetaTags from "react-meta-tags";



import answer_icon from '../media/answer_icon.svg';

const getSeoByUrl = seoActions.getSeoByUrl;


class Support extends Component {

    constructor(props){
        super(props);
     
    }

    componentDidMount(){
        this.props.getSeoByUrl('support')
        this.preSets();
    }

    preSets(){
      d3.selectAll('.answer_box')
        .each(function(){
          let e = d3.select(this);
          e.attr('data-open', false);
          d3.select(this).select('.support_read_more')
            .on('click', function(){
              let isOpen = e.attr('data-open');
              d3.select(this).select('span').node().innerHTML =  `${isOpen === 'false' ? 'CLOSE' : 'READ MORE'}`;
              e.select('p')
                .transition().duration(500).ease(d3.easeLinear)
                .style('max-height',  `${isOpen === 'false' ? 'fit-content' : '48px'}`);
              e.attr('data-open', `${isOpen === 'false' ? 'true' : 'false'}`);
            });
          })
      }

    readMore(url, lang){
        return (
            <a to={url}>
                <div className='support_read_more' style={{background: '#000'}}>
                    <span style={{color: '#fff'}}>{lang === 'ru' ? 'Подробнее' : 'READ MORE'}</span>
                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 21.767 31.777">
                        <path fill='#000' d="M116.4,20V34.406a3.829,3.829,0,0,0,3.558,3.947h0a3.692,3.692,0,0,0,3.558-3.947h0a1.35,1.35,0,0,1,1.256-1.382h2.512a1.35,1.35,0,0,1,1.256,1.382V46.248a5.04,5.04,0,0,0,4.814,5.329h0a5.04,5.04,0,0,0,4.814-5.329L137.958,19.8Z" transform="translate(-116.4 -19.8)"/>
                    </svg>
                </div>
            </a>
        )
    }

    render(){
        let metaTags = this.props.getMetaResult && this.props.getMetaResult.result;
        return(
            <div className='support_page'>
 <MetaTags>
            <title>{metaTags && metaTags.title}</title>
            <meta name="description" content={metaTags && metaTags.description} />
            <meta name="keywords" content={metaTags && metaTags.keywords} />
            </MetaTags>
                <Header page='support'/>
                {
                    this.props.lang === 'ru' ? ru.support(this.readMore, answer_icon) : eng.support(this.readMore, answer_icon)
                }
                {/* <section className='support_section'>
                   <div className='container'>
                        <h2>FIND SOLUTION<br/> FOR YOUR PECULIARITIES</h2>
                        <p>Offering broad services, we`re ready to meet all your special<br/>necessities</p>
                        <div className='answer_list'>
                            <div className='answer_box'>
                                <img src={answer_icon} alt='' />
                                <h3>What permission do you require? </h3>
                                <p>Ukraine is always ready to meet the wishes and Ukraine is always ready to meet the wishes and demandUkraine is always ready to meet the wishes and demandUkraine is always ready to meet the wishes and demandUkraine is always ready to meet the wishes and demandUkrUkraine is always ready to meet the wishes and demandaine is always ready to meet the wishes and demand demands of every production process.  Conditions of shooting are one of the best in a Europe. You can shoot anywhere you want in the public places. If it’s a shooting of the  private property or production, which  requires ground control, getting permission may last for up to 1 week. It’s so important to point out that it’s allowed to shoot on more than 20 locations which are forbidden in the other countries. </p>
                                {this.readMore('#')}
                            </div>
                            <div className='answer_box'>
                                <img src={answer_icon} alt='' />
                                <h3>What permission do you require? </h3>
                                <p>Ukraine is always ready to meet the wishes and demands of every production process.  Conditions of shooting are one of the best in a Europe. You can shoot anywhere you want in the public places. If it’s a shooting of the  private property or production, which  requires ground control, getting permission may last for up to 1 week. It’s so important to point out that it’s allowed to shoot on more than 20 locations which are forbidden in the other countries. </p>
                                {this.readMore('#')}
                            </div>
                            <div className='answer_box'>
                                <img src={answer_icon} alt='' />
                                <h3>What permission do you require? </h3>
                                <p>Ukraine is always ready to meet the wishes and demands of every production process.  Conditions of shooting are one of the best in a Europe. You can shoot anywhere you want in the public places. If it’s a shooting of the  private property or production, which  requires ground control, getting permission may last for up to 1 week. It’s so important to point out that it’s allowed to shoot on more than 20 locations which are forbidden in the other countries. </p>
                                {this.readMore('#')}
                            </div>
                        </div>
                   </div>
                </section> */}
                <Footer/>
            </div>
        )
    }

}

const mapStateToProps = (state) => {
    return {
        lang: state.lang.currentLang,
        getMetaResult: state.seo.getSeoByUrlResult
    }
};

export default connect(mapStateToProps, { getSeoByUrl })(Support);


