import React, {Component} from 'react';
import Header from './Header'; /* Adaptive */
import ServiceBar from './ServiceBar'; /* Adaptive */
import Footer from './Footer'; /* Adaptive */
import '../Styles/Service.css';
import { seoActions } from '../actions/seoActions';
import MetaTags from "react-meta-tags";
import { connect } from 'react-redux';
const getSeoByUrl = seoActions.getSeoByUrl;

class Service extends Component {

    constructor(props){
        super(props);
     
    }

    componentDidMount(){
        this.props.getSeoByUrl('service')
    }



    render(){
        let metaTags = this.props.getMetaResult && this.props.getMetaResult.result;
        return(
            <div className='service_page'>
 <MetaTags>
            <title>{metaTags && metaTags.title}</title>
            <meta name="description" content={metaTags && metaTags.description} />
            <meta name="keywords" content={metaTags && metaTags.keywords} />
            </MetaTags>
                <Header page='service'/>
                <section className='service_preview'>
                    <div className='container'>
                        <h2>OFFERING LOCAL PRODUCTION</h2>
                        <p>Dignissim qui blandit praesent luptatum zzril delenit augue duis dolore te feugait nulla facilisi.</p>
                    </div>
                </section>
                <section className='service_info'>
                    <div className='container'>
                        <p>Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diam nonummy nibh euismod tincidunt ut laoreet dolore magna aliquam erat volutpat. </p>
                        <p>Ut wisi enim ad minim veniam, quis nostrud exerci tation ullamcorper suscipit lobortis nisl ut aliquip ex ea commodo consequat. Duis autem vel eum iriure dolor in hendrerit in vulputate velit esse molestie consequat, vel illum dolore eu feugiat nulla facilisis at vero eros et accumsan et iusto odio dignissim qui blandit praesent luptatum zzril delenit augue duis dolore te feugait nulla facilisi. </p>
                        <p>Dignissim qui blandit praesent luptatum zzril delenit augue duis dolore te feugait nulla facilisi.</p>
                        <ServiceBar/>
                    </div>
                </section>
                <Footer/>
            </div>
        )
    }

}

const mapStateToProps = (state) => {
    return {
        lang: state.lang.currentLang,
        getMetaResult: state.seo.getSeoByUrlResult
    }
};

export default connect(mapStateToProps, { getSeoByUrl })(Service);