import React, {Component} from 'react';
import { Redirect, Link } from 'react-router-dom';
import Header from './Header';
import Footer from './Footer';
import * as d3 from 'd3';
import '../Styles/Post.css';
import { apiUrl } from '../config';
const noImage = 'https://upload.wikimedia.org/wikipedia/commons/thumb/6/6c/No_image_3x4.svg/1280px-No_image_3x4.svg.png';


export default class extends Component {

    constructor(props){
        super(props);
        this.state = {
          error: false,
            // location: {}
        }
    }

    componentDidMount(){
        const locationUrl = this.props.match.url.split('/')[2];
        fetch(`${apiUrl}/location/${locationUrl}`)
            .then(res => {
                if(res.status === 404) {
                    this.setState({ error: true })
                }
                else return res.json();
            })
            .then(location => this.setState({location}, () => console.log(this.state.location)))
    }
    

    


    render(){
        if(!this.state.error){
        return(
            <div className='blog_post_view'>
                <Header page='locations'/>
                <section className='blog_post_section'>
                   <div className='post_view_container'>
                        <Link to="/locations" className='post_button blog_post_exit_button'>
                            <span style={{color: '#000'}}>Go back</span>
                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 21.767 31.777">
                                <path fill='#fff' d="M116.4,20V34.406a3.829,3.829,0,0,0,3.558,3.947h0a3.692,3.692,0,0,0,3.558-3.947h0a1.35,1.35,0,0,1,1.256-1.382h2.512a1.35,1.35,0,0,1,1.256,1.382V46.248a5.04,5.04,0,0,0,4.814,5.329h0a5.04,5.04,0,0,0,4.814-5.329L137.958,19.8Z" transform="translate(-116.4 -19.8)"/>
                            </svg>
                        </Link>
                        {
                          this.state.location && 
                          <div className='blog_post_view_info_box'>
                            <div dangerouslySetInnerHTML={{__html: this.state.location.content.extended}}>
                            </div>
                            <img src={this.state.location.heroImage.secure_url || noImage} alt=""/>
                          </div>
                        }
                        <div className='post_button location_post_link_button'>
                            <span style={{color: '#000'}}>link</span>
                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 21.767 31.777">
                                <path fill='#fff' d="M116.4,20V34.406a3.829,3.829,0,0,0,3.558,3.947h0a3.692,3.692,0,0,0,3.558-3.947h0a1.35,1.35,0,0,1,1.256-1.382h2.512a1.35,1.35,0,0,1,1.256,1.382V46.248a5.04,5.04,0,0,0,4.814,5.329h0a5.04,5.04,0,0,0,4.814-5.329L137.958,19.8Z" transform="translate(-116.4 -19.8)"/>
                            </svg>
                        </div>
                   </div>
                </section>
                <Footer/>
            </div>
        )
    }else {
        return(<Redirect to="/locations"/>)
    }
    }

}