// import { types } from './types';
import { seoServices } from '../services/seo.services';

const getSeoPages = () => dispatch => {
  return seoServices.getSeoPages()
    .then(result => dispatch({
      type: 'GET_SEO_PAGES',
      payload: result
    }));
};

const getSeoByUrl = url => dispatch => {
  return seoServices.getSeoByUrl(url)
    .then(result => dispatch({
      type: 'GET_SEO_BY_URL',
      payload: result
    }));
};

const getArticleSeoByUrl = url => dispatch => {
  return seoServices.getArticleSeoByUrl(url)
    .then(result => dispatch({
      type: 'GET_ARTICLE_SEO_BY_URL',
      payload: result
    }));
};

const editSeoPage = (pageId, seoData) => dispatch => {
  return seoServices.editSeoPages(pageId, seoData)
    .then(result => dispatch({
      type: 'EDIT_SEO_PAGE',
      payload: result
    }));
};

export const seoActions = {
  getSeoPages,
  getSeoByUrl,
  getArticleSeoByUrl,
  editSeoPage
};